import { v4 as uuid } from 'uuid';

import { i18n as VueI18nInstance } from 'src/boot/i18n';
import i18n from './introduction';

import { ucFirst } from 'src/helpers/utils';
import { _store } from 'src/store';

import config from 'src/config/config';
import Connection from 'src/controllers/Connection';
import ioBroker from 'src/controllers/ioBroker';

const layoutTemplate = require('src/config/templates/tour-introduction.layout.json');
const widgetTemplate = require('src/config/templates/tour-introduction.widgets.json');

i18n.register(VueI18nInstance, i18n.translations);
const _clone = require('rfdc')();

export default [
	{
		'title': '#introduction.title',
		'message': '#introduction.content',
		'onEnter': ({ props }) => {
			const socket = Connection.getConnection;
			socket.setState(config.get('NODE_SETTINGS'), JSON.stringify({ ..._store.state.jarvis.settings, ['tour' + ucFirst(props.tour)]: true }));
		}
	},
	{
		'target': '#SettingsButton',
		'spotlight': '.q-drawer.q-drawer--left',
		'title': '#settings.title',
		'message': '#settings.content',
		'secondaryButton': '#settings.buttons.manually',
		'secondaryAction': (tour, step) => {
			tour.finish();
			document.querySelector('#SettingsButton').click();
		},
		'primaryButton': '#settings.buttons.automatically',
		'primaryAction': (tour, step) => {
			tour.nextStep();
			
			const devices = {}
			const deviceList = {}
			
			ioBroker.importDevices(config.get('DEFAULT_ADAPTERIMPORT').concat([ 'daswetter' ]))
				.then(result => {
					result.forEach(res => {
						if (res.status === 'fulfilled') {
							const device = res.value;
							
							devices[device.function] = devices[device.function] || [];
							devices[device.function].push(device);
							deviceList[device.id] = device;
						}
					});
					
					// get layout from template
					const layout = layoutTemplate; // _clone(layoutTemplate);
					const widgets = widgetTemplate;
					
					/*
					// change config of Weather module
					const weather = Cache.get('daswetter');
					if (weather) {
						// add credentials
						layout[0].tabs[0].columns[0][1].moduleConfig = {
							"apikey": weather.affiliate_id,
							"localid": weather.localidad
						}
					}
					else {
						// remove widget if credentials could not be retrieved
						layout[0].tabs[0].columns[0].splice(1, 1);
					}
					*/
					
					// define widget template
					const tplWidget = {
						"config": {},
						"items": [],
						"module": "StateList",
						"title": ""
					}
					
					// define functions which devices shall be adapted
					const widgetFunctions = [
						{ 'widgetId': '3958dfb0-83da-48ae-96f3-e66969a92cac', 'function': 'light', 'icon': 'mdi-lightbulb-on' },
						{ 'widgetId': 'be31acad-6bb4-40ed-a9cf-24cd981d8545', 'function': 'blind', 'icon': 'mdi-window-shutter-open' },
						{ 'widgetId': 'b073226b-14d9-40e9-bbd7-4a54080cfa87', 'function': 'heating', 'icon': 'mdi-radiator' },
						{ 'widgetId': '99142690-4245-445b-bbc4-be8d09b93b83', 'function': 'window', 'icon': 'mdi-window-closed-variant' }
					];
					
					// add devices
					widgetFunctions.forEach((func, i) => {
						const widget = _clone(tplWidget);
						widget.id = func.widgetId;
						widget.title = VueI18nInstance.t(func.function);
						widget.icon = func.icon;
						
						devices[func.function] = devices[func.function] || [];
						for (const device of devices[func.function]) {
							if (!device.states) {
								continue;
							}
							
							const deviceStateList = Object.keys(device.states);
							let d = {
								"id": uuid(),
								"type": "device",
								"deviceId": device.id,
								"showState": true,
								"primaryStateKey": deviceStateList[0] || '',
								"secondaryStatesKeys": []
							}
							
							if (device.function === 'light' && (deviceStateList.includes('power') || deviceStateList.includes('level'))) {
								d = {
									...d,
									"showState": false,
									"actionElement": "SwitchAction",
									"primaryStateKey": deviceStateList.includes('power') ? 'power' : 'level'
								}
							}
							else if (device.function === 'blind' && deviceStateList.includes('level')) {
								d = {
									...d,
									"showState": false,
									"actionElement": "BlindLevelAction",
									"primaryStateKey": "level"
								}
							}
							else if (device.function === 'heating' && deviceStateList.includes('temperature')) {
								d = {
									...d,
									"showState": true,
									"primaryStateKey": "temperature",
									"secondaryStatesKeys": deviceStateList.filter(secondaryStatesKey => [ 'setTemperature', 'humidity' ].includes(secondaryStatesKey))
								}
							}
							else if (device.function === 'window' && deviceStateList.includes('open')) {
								d = {
									...d,
									"primaryStateKey": "open"
								}
							}
							
							widget.items.push(d);
						}
						
						// add widget
						widgets[widget.id] = widget;
						
						// adjust layout height based on StateList items
						const widgetHeight = Math.ceil(widget.items.length * 48 / 100);
						layout[0].tabs[0].widgetsDesktop[i].h = widgetHeight;
						
						if (layout[0].tabs[0].widgetsDesktop[i + 1] && layout[0].tabs[0].widgetsDesktop[i].x === layout[0].tabs[0].widgetsDesktop[i + 1].x) {
							layout[0].tabs[0].widgetsDesktop[i + 1].y = widgetHeight;
						}
					});
					
					// save
					const socket = Connection.getConnection;
					const promises = [
						socket.setState(config.get('NODE_LAYOUT'), JSON.stringify({ "version": 3, layout })),
						socket.setState(config.get('NODE_DEVICES'), JSON.stringify({ "version": 3, "devices": deviceList })),
						socket.setState(config.get('NODE_WIDGETS'), JSON.stringify(widgets)),
						socket.setState(config.get('NODE_SETTINGS'), JSON.stringify({ ..._store.state.jarvis.settings, 'tourInterface': false }))
					];
					
					Promise.all(promises)
						.then(() => {
							tour.finish();
						})
						.catch(error => console.error(error));
				})
				.catch(error => {
					console.error(error);
				});
		}
	},
	{
		'style': {
			'max-width': '450px'
		},
		'title': '#jarvis.title',
		'loading': true,
		'buttons': false
	}
];
