<template>
	<div class="q-mt-xs NotificationList">
		<q-separator/>
		<q-list>
			<div v-for="notification in notificationsActive" :key="notification.id">
				<notification-item :notification="notification" />
			</div>
		</q-list>
		
		<q-btn flat class="full-width" @click="toggle()" v-show="notificationsArchived && notificationsArchived.length > 0">
			<divider v-if="showArchived" :label="$t('hide archived notifications')" />
			<divider v-else :label="$t('show archived notifications')" />
		</q-btn>
		
		<div v-if="showArchived">
			<q-separator/>
			<q-list>
				<div v-for="notification in notificationsArchived" :key="notification.id">
					<notification-item :notification="notification" />
				</div>
			</q-list>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

import Divider from '../../components/Divider.vue'
import NotificationItem from './Item.vue'

export default defineComponent({
	'name': 'NotificationList',
	
	'components': {
		NotificationItem,
		Divider
	},
	
	data() {
		return {
			'showArchived': false
		}
	},
	
	'methods': {
		toggle() {
			this.showArchived = !this.showArchived
		}
	},
	
	'computed': {
		notificationsActive() {
			return this.$store.getters['notifications/getActive'];
		},
		notificationsArchived() {
			return this.$store.getters['notifications/getArchived'];
		}
	}
});
</script>

<style>
.NotificationList {
	max-height: calc(100% - 40px - 8px);
	overflow-y: scroll;
}
</style>
