<template>
	<div
		class="jarvis-widget widget bgcolor"
		:class="{
			'tile': tab.widgetEdges,
			'paper': !tab.widgetEdges
		}"
		:style="scaleToFitContents === false ? { 'overflow-y': 'auto' } : {}"
		>
		
		<q-card
			flat
			class="jarvis-module module full-width full-height"
			:class="'jarvis-module-' + widget.module"
			>
			
			<q-item class="jarvis-widget-header" v-if="widget.hideTitle !== true && (widget.icon || widget.title)" ref="widgetTitle">
				<q-item-section side v-if="widget.icon" :class="{ 'cursor-pointer': widget.link !== '' }" @click="jump">
					<q-avatar :square="tab.widgetEdges" :rounded="!tab.widgetEdges" class="widget-icon jarvis-widget-icon" font-size="24px">
						<icon :name="widget.icon" />
					</q-avatar>
				</q-item-section>

				<q-item-section>
					<q-item-label class="text-h6" :class="{ 'cursor-pointer': widget.link !== '' }" @click="jump">
						{{ widget.title }}
					</q-item-label>
				</q-item-section>
			</q-item>
			<q-separator v-if="widget.hideTitle !== true && (widget.icon || widget.title)" />
			
			<q-card-section horizontal class="jarvis-widget-content widget-content"> <!--  :style="{ width: scaleSizes.width, height: scaleSizes.height }" -->
				<q-resize-observer @resize="onResize" />
				<div
					class="widget-scaler full-width"
					:style="scaleStyle"
					>
					
					<component
						:is="modules[widget.module]"
						:widget="widget"
						v-bind="{...$props, ...$attrs}"
						class="full-width"
						:_containerSize="{
							'width': containerSize.width,
							'height': containerSize.height - widgetTitleSize
						}"
						:_widgetSize="{
							'width': size.width,
							'height': size.height // - widgetTitleSize
						}"
						/>
					
				</div>
			</q-card-section>
		</q-card>
	</div>
</template>

<script lang="ts">
import { defineComponent } from '@vue/composition-api';

import Modules, { ScaleExceptions as SCALE_EXCEPTIONS } from 'src/modules';

export default defineComponent({
	'name': 'Widget',
	'props': [ 'page', 'tab', 'widgets', 'config', 'widgetSize', 'containerSize' ],
	
	mounted() {
		this.widgetTitleSize = (this.$refs.widgetTitle && this.$refs.widgetTitle.$el.offsetHeight) || 0;
	},
	
	'computed': {
		scale() {
			if (this.scaleToFitContents === false || SCALE_EXCEPTIONS.includes(this.widget.module)) {
				return { 'x': 1, 'y': 1 }
			}
			
			const scale = {
				'x': this.size ? Math.min(1, this.containerSize.width / this.size.width) : 1,
				'y': this.size ? Math.min(1, (this.containerSize.height - this.widgetTitleSize) / this.size.height) : 1
			}
			
			return scale;
		},
		position() {
			const position = [ 0, 0 ];
			position[0] = this.alignmentHorizontal === 'center' ? '50%' : (this.alignmentHorizontal === 'right' ? '100%' : this.alignmentHorizontal);
			position[1] = this.alignmentVertical === 'middle' ? '50%' : (this.alignmentVertical === 'bottom' ? '100%' : this.alignmentVertical);
			return position;
		},
		scaleSizes() {
			return {
				'width': this.alignmentHorizontal !== 'fill-width' ? '100%' : Math.floor(1 / (this.scale.y < this.scale.x ? this.scale.y : this.scale.x) * 100) + '% !important',
				'height': (this.containerSize.height - this.widgetTitleSize) + 'px'
			}
		},
		scaleStyle() {
			const style = {
				//'transform': 'scaleX(' + scale.x + ')',
				'transform': 'scale(' + (this.scale.y < this.scale.x ? this.scale.y : this.scale.x) + ')',
				'transform-origin': this.position.join(' ')
			}
			
			if (this.alignmentVertical === 'middle') {
				style.margin = 'auto';
			}
			
			return style;
		},
		widget() {
			const widgetId = this.widgets[0];
			return this.$store.state.jarvis.widgets[widgetId] || {};
		}
	},
	
	'methods': {
		onResize(size) {
			this.resizer && clearTimeout(this.resizer);
			this.resizer = setTimeout(() => {
				this.size = size;
			}, 0);
		},
		jump() {
			// URL
			if (this.widget.link && (this.widget.link.indexOf('http') > -1 || this.widget.link.indexOf('www') > -1)) {
				window.open((this.widget.link.indexOf('http') === -1 ? 'http://' : '') + this.widget.link, '_blank');
			}
			
			// Tab ID
			else if (this.widget.link) {
				this.$router.push({ 'params': { 'tabId': this.widget.link } }).catch(error => console.warn(error));
			}
		}
	},
	
	data() {
		this.modules = Modules._Modules;
		
		let { alignmentHorizontal, alignmentVertical, scaleToFitContents } = this.config;
		alignmentHorizontal = alignmentHorizontal || 'center';
		alignmentVertical = alignmentVertical || 'top';
		scaleToFitContents = scaleToFitContents !== undefined ? scaleToFitContents : true;
		
		return {
			scaleToFitContents,
			alignmentHorizontal,
			alignmentVertical,
			'size': {
				'width': 0,
				'height': 0
			},
			'widgetTitleSize': 0,
			'resizer': null
		}
	}
});
</script>

<style lang="scss">
.widget {
	/*overflow: hidden; /* necessary */
	width: 100%;
	height: 100%;
	margin: 0 !important;
}

.widget-scaler {
	transform-origin: 0 0;
}

.widget-icon {
	background-color: $primary;
	color: #fff;
}

.widget .module {
	/*margin-bottom: 16px;*/
}
</style>
