	"#WidgetContent.title": "Widget Inhalt öffnet die Geräte-Details",
	"#WidgetContent.content": "Dieses Widget nutzt das StateList Modul, um die Geräte anzuzeigen inkl. der Möglichkeit einen Datenpunkt auszulösen (z. B. an- bzw. auszuschalten).\n\nDie Icons eines jeden Geräts können beliebig - auch in Relation zum Wert des Datenpunkts - angepasst werden (also ein jeweils anderes Icon für ein offenes oder geschlossenes Fenster). Es kann entweder der Wert des Datenpunkts angezeigt werden oder eine Möglichkeit den Datenpunkt zu schalten. Die Möglichkeit zum Schalten umfasst einen Switch, Buttons, Eingabefelder, etc.\n\n**Zum Fortfahren bitte das Gerät klicken, um die Geräte-Details öffnen.**",
	
