<template>
	<div :style=" { 'height': (windowHeight - 44) + 'px' }" class="q-mt-xs">
		<q-list dense :style="{ 'overflow-y': expanded ? 'scroll' : 'hidden', 'maxHeight': (windowHeight - (header ? 40 : 0) - positionPageEndHeight) + 'px' }">
			<div
				v-for="item of drawerItems"
				:key="item.id"
				>
				
				<q-separator spaced v-if="item.type === 'divider'" />
				<q-item-label header v-if="item.type === 'header'" v-show="expanded">{{ item.title }}</q-item-label>
				<div class="headerInvisible" v-if="item.type === 'header'" v-show="!expanded">&nbsp;</div>
				
				<q-item
					v-if="!item.type || item.type === 'page'"
					:to="{ params: { tabId: item.tabs && item.tabs[0] && item.tabs[0].id } }"
					clickable
					@click="setPage(item)"
					class="full-width"
					:style="{ backgroundColor: item.backgroundColor, color: item.color }"
					:class="{ 'selected': item.id === pageSelected }"
					active-class="selected"
					>
					
					<q-item-section
						avatar
						center
						:class="{ 'selectedText': item.id === pageSelected }"
						>
						
						<icon :name="item.icon" :color="item.iconColor ? item.iconColor : (item.id === pageSelected ? 'white' : 'primary')" />
					</q-item-section>
					
					<q-item-section>
						<q-item-label :class="{ 'selectedText': item.id === pageSelected }">{{ item.title }}</q-item-label>
					</q-item-section>
				</q-item>
				<div v-if="!item.type || item.type === 'page'" class="spacer" :class="{ 'selected': item.id === pageSelected }"></div>
			</div>
		</q-list>
		
		<q-list ref="positionPageEnd" class="positionPageEnd">
			<q-separator spaced />
			<q-item-label v-show="expanded" header>jarvis</q-item-label>
			<div v-show="!expanded" class="headerInvisible">&nbsp;</div>
			
			<q-item clickable v-ripple to="/configuration/" id="SettingsButton">
				<q-item-section avatar>
					<icon name="mdi-cog" color="primary" />
				</q-item-section>
				<q-item-section>{{ $t('Configuration') }} & {{ $t('Help') }}</q-item-section>
			</q-item>
		</q-list>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import Pro from 'src/controllers/Pro';

import i18n from './i18n';
export default defineComponent({
	'name': 'Sidebar',
	'props': [ 'expanded', 'header' ],
	
	data() {
		return {
			'positionPageEndHeight': 0
		}
	},
	
	'methods': {
		setPage(page) {
			const tabId = page.tabs?.[0].id;
			
			this.$store.commit('jarvis/set', {
				'selectedTabId': tabId,
				'selectedPageId': page.id,
				'drawerLeftOpen': false
			});
		}
	},
	
	mounted() {
		this.positionPageEndHeight = this.$refs.positionPageEnd.$el.clientHeight;
	},
	
	'computed': {
		pageSelected() {
			const selectedPageId = this.$store.getters['jarvis/getSelectedPageId'];
			return selectedPageId;
		},
		drawerItems() {
			const drawerItems = this.$store.state.jarvis.layout || [];
			return Pro.isPro() ? drawerItems : drawerItems.slice(0, 5);
		},
		windowHeight() {
			return window.innerHeight - (this.header !== true ? 40 : 0);
		}
	},
	
	created() {
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>

<style lang="scss">
.positionPageEnd {
	position: absolute;
	bottom: 0;
	width: 100%;
}

.selected {
	background-color: $primary;
	color: #fff;
}

.selectedText {
	color: #fff;
}

.spacer {
	width: 10px;
}

.headerInvisible {
	padding: 16px;
	font-size: 0.875rem;
	line-height: 1.2em;
}
</style>
