<template>
	<div>
		<q-overlay v-model="overlay" :no-scroll="true" :z-index="5000" :opacity="0.7" />
		
		<v-tour
			name="tour"
			:steps="steps"
			:callbacks="callbacks"
			>
			
			<template slot-scope="tour">
				<transition name="fade">
					<v-step
						v-if="tour.steps[tour.currentStep]"
						:key="tour.currentStep"
						:step="tour.steps[tour.currentStep]"
						:previous-step="tour.previousStep"
						:next-step="tour.nextStep"
						:stop="tour.stop"
						:skip="tour.skip"
						:is-first="tour.isFirst"
						:is-last="tour.isLast"
						:labels="tour.labels"
						:set="step = tour.steps[tour.currentStep]"
						:style="tour.steps[tour.currentStep].style || {}"
						>
						
						<template slot="header">
							<div/>
						</template>
						
						<template slot="content">
							<q-card>
								<q-card-section>
									<div class="text-h6">{{ $t(step.title) }}</div>
								</q-card-section>
								<q-separator />
								
								<q-linear-progress indeterminate v-if="step.loading === true" />
								<q-card-section style="max-height: 50vh" class="body scroll">
									<div v-html="_markdown.render($t(step.message))" />
								</q-card-section>
								<q-separator />

								<q-card-actions class="nowrap" v-if="step.buttons !== false">
									<q-btn @click="tour.skip" flat :label="$t('Skip')" color="accent" />
									<q-space />
									
									<q-btn v-if="step.secondaryAction === undefined" v-show="!tour.isFirst" @click="tour.previousStep" flat :label="$t('Previous')" color="secondary" />
									<q-btn v-else v-show="!tour.isFirst" @click="step.secondaryAction(tour, step)" flat :label="$t(step.secondaryButton)" color="secondary" />
									
									<q-btn v-if="step.primaryButton === undefined" v-show="!tour.isLast" @click="tour.nextStep" flat :label="$t('Next')" color="primary" />
									<q-btn v-if="step.primaryButton === undefined" v-show="tour.isLast" @click="tour.finish" :label="$t('Finish')" color="primary" />
									<q-btn v-else v-show="!tour.isFirst" @click="step.primaryAction(tour, step)" flat :label="$t(step.primaryButton)" color="primary" />
								</q-card-actions>
							</q-card>
						</template>
						
						<template slot="actions">
							<div/>
						</template>
					</v-step>
				</transition>
			</template>
		</v-tour>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import Vue from 'vue';
import VueTour from 'vue-tour';

import { QOverlay } from '@quasar/quasar-ui-qoverlay';

require('vue-tour/dist/vue-tour.css');
Vue.use(VueTour);

import { ucFirst } from 'src/helpers/utils';
import TOURS from 'src/config/tours';

const _markdown = require('markdown-it')();
export default defineComponent({
	'name': 'Tours',
	'props': [ 'tour' ],
	
	'components': {
		QOverlay
	},
	
	'methods': {
		highlight(step, state = true) {
			const target = step.spotlight || step.target;
			
			if (target) {
				const el = document.querySelector(target);
				if (el) {
					el.style.zIndex = state ? 5001 : null;
					el.style.pointerEvents = state && step.onSpotlightClick === undefined ? 'none' : 'unset';
					
					// add click listener
					if (step.onSpotlightClick !== undefined) {
						el.onclick = () => step.onSpotlightClick({ 'props': this.$props, 'tour': this.$tours.tour, step });
					}
					
					// add target styles (state = true)
					if (step.targetStyle && state === true) {
						this.styles = {};
						for (const key in step.targetStyle) {
							this.styles[key] = el.style[key];
							el.style[key] = step.targetStyle[key];
						}
					}
					
					// remove target styles (state = false)
					else if (step.targetStyle && state === false) {
						for (const key in step.targetStyle) {
							el.style[key] = this.styles[key];
						}
					}
				}
			}
		},
		onStart() {
			this.overlay = true;
			this.onNextStep(-1);
		},
		onPreviousStep(currentStep) {
			const previousStep = currentStep - 1;
			this.currentStep = previousStep;
			
			this.highlight(this.steps[currentStep], false);
			this.highlight(this.steps[previousStep]);
		},
		onNextStep(currentStep) {
			const nextStep = currentStep + 1;
			this.currentStep = nextStep;
			
			this.steps[currentStep] && this.highlight(this.steps[currentStep], false);
			this.steps[nextStep] && this.highlight(this.steps[nextStep]);
			
			// callbacks
			if (this.steps[currentStep] && this.steps[currentStep].onLeave) {
				this.steps[currentStep].onLeave({ 'props': this.$props, 'tour': this.$tours.tour, 'step': this.steps[currentStep] });
			}
			
			if (this.steps[nextStep] && this.steps[nextStep].onEnter) {
				this.steps[nextStep].onEnter({ 'props': this.$props, 'tour': this.$tours.tour, 'step': this.steps[nextStep] });
			}
		},
		onSkip() {
			this.overlay = false;
			this.steps[this.currentStep] && this.highlight(this.steps[this.currentStep], false);
		},
		onFinish() {
			this.overlay = false;
			this.steps[this.currentStep] && this.highlight(this.steps[this.currentStep], false);
		}
	},
	
	data() {
		this._markdown = _markdown;
		this.styles = {};
		
		return {
			'callbacks': {
				'onStart': this.onStart,
				'onPreviousStep': this.onPreviousStep,
				'onNextStep': this.onNextStep,
				'onSkip': this.onSkip,
				'onFinish': this.onFinish
			},
			'overlay': false,
			'currentStep': 0,
			'steps': TOURS[this.tour] || []
		}
	},
	
	mounted() {
		if (this.tour && this.$store.state.jarvis.settings['tour' + ucFirst(this.tour)] !== true) {
			this.$tours.tour.start();
		}
	}
});
</script>

<style src="@quasar/quasar-ui-qoverlay/dist/index.css"></style>
<style lang="scss">
.v-step {
	background: none !important;
	text-align: left !important;
	padding: 0 !important;
	box-shadow: none !important;
	-webkit-box-shadow: none !important;
	min-width: 600px !important;
	color: #000 !important;
}

.v-step .v-step__arrow::before {
	background: #fff !important;
	margin-left: 0 !important;
}

.body--dark .v-step .v-step__arrow::before {
	background: var(--q-color-dark) !important;
}

.spotlight {
	position: absolute;
	left: 50px;
	top: 50px;
	width: 200px;
	height: 200px;
	border-radius: 20px;
	box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.5) inset, 0px 0px 0px 1000px rgba(0,0,0,0.5);
	z-index: 5001;
}
</style>
