import { defineComponent } from '@vue/composition-api'

import Pro from 'src/controllers/Pro';

import DeviceDetailsPopup from './Devices';
import NotificationDrawer from './Notifications';
import NotificationEventSnackbar from './Notifications/EventSnackbar.vue';
import NotificationEventPopup from './Notifications/EventPopup.vue';
import Page from './Page';
import Sidebar from './Sidebar';
import tours from '../components/tours';

export default defineComponent({
	'name': 'Jarvis',
	
	'components': {
		DeviceDetailsPopup,
		NotificationDrawer,
		NotificationEventSnackbar,
		NotificationEventPopup,
		Page,
		Sidebar,
		tours
	},
	
	mounted() {
		if (Pro.isPro()) {
			// inject custom css
			const styleTag = document.getElementById('customStyles') || document.createElement('style');
			styleTag.innerHTML = this.$store.state.jarvis.styles;
			styleTag.id = 'customStyles';
			
			const headTag = document.getElementsByTagName('head');
			headTag[0].appendChild(styleTag);
		}
	},
	
	'methods': {
		reveal(val) {
			this.$store.commit('jarvis/set', {
				'headerVisible': val
			});
		},
		onResize(tabs) {
			this.enableTabsDropdown = false;
			this.hiddenTabs = [];
			
			const pageWidth = this.$q.screen.width;
			
			if (this.$refs.tabs) {
				this.$refs.tabs.forEach((tab, i) => {
					tab.$el.style.display = 'inherit';
					const position = tab.$el.offsetLeft + tab.$el.offsetWidth + 200;
					
					if (position > pageWidth) {
						tab.$el.style.display = 'none';
						this.enableTabsDropdown = true;
						this.hiddenTabs.push(this.tabs[i]); // tabId
					}
				});
			}
		},
		toggleDrawerLeft(val) {
			//if ((e && typeof e === 'object') || (typeof e === 'boolean' && e === false)) { // e.type === 'click'
			
			this.$store.commit('jarvis/set', {
				'drawerLeftOpen': val
			});
		},
		toggleDrawerRight(val) {
			this.$store.commit('jarvis/set', {
				'drawerRightOpen': val
			});
		},
		pageContainer(offset, height) {
			this.$store.commit('jarvis/set', { 'pageHeight': (height - offset) });
			return {}; // { 'height': (height-offset) + 'px' }
		},
		hideDropdown() {
			setTimeout(() => {
				this.hiddenTabsDropdown = false;
			}, 500);
		}
	},
	
	data() {
		return {
			'enableTabsDropdown': false,
			'hiddenTabs': [],
			'hiddenTabsDropdown': false,
			'drawerLeftMini': true
		}
	},
	
	'watch': {
		page(newVal) {
			setTimeout(() => {
				this.onResize();
			}, 0);
		}
	},
	
	'computed': {
		headerVisible() {
			return this.$store.state.jarvis.headerVisible;
		},
		drawerMiniMode() {
			return this.$store.state.jarvis.settings.drawerMiniMode !== undefined ? this.$store.state.jarvis.settings.drawerMiniMode : false;
		},
		drawerLeft() {
			return this.$store.state.jarvis.drawerLeftOpen;
		},
		drawerRight() {
			return this.$store.state.jarvis.drawerRightOpen;
		},
		page() {
			return this.$store.getters['jarvis/getSelectedPage'] || {};
		},
		tabs() {
			const tabs = this.page.tabs || [];
			return Pro.isPro() ? tabs : tabs.slice(0, 5);
		},
		tabsBottom() {
			return this.$store.state.jarvis.settings.tabsBottom !== undefined ? this.$store.state.jarvis.settings.tabsBottom : false;
		},
		tabsShow() {
			return this.$store.state.jarvis.settings.tabsShow !== undefined ? this.$store.state.jarvis.settings.tabsShow : 'hide';
		},
		'tabId': {
			get() {
				const tabId = this.$store.getters['jarvis/getSelectedTabId'];
				return tabId;
			},
			set(tabId) {
				if (tabId) {
					this.$store.commit('jarvis/set', { 'selectedTabId': tabId });
				}
			}
		},
		tabsCount() {
			return Object.keys(this.tabs).length;
		},
		unreadNotificationsCount() {
			return this.$store.getters['notifications/getUnread'].length;
		}
	}
});
