<template>
	<q-dialog
		:value="deviceId !== null"
		@hide="onClose"
		>
		
		<q-card class="column jarvis-DeviceDetailsPopup" style="width: 800px; max-width: 80vw; flex-wrap: nowrap">
			<q-card-section class="row items-center">
				<div class="text-h6">{{ device.name }}</div>
				
				<q-space />
				<q-btn flat round color="grey" icon="close" @click="onClose" />
			</q-card-section>
			<q-separator />
			
			<q-card-section class="col q-pa-none scroll body">
				<state-list
					:openDeviceDetails="false"
					:widget="widget"
					/>
				
			</q-card-section>
			
			<q-separator />
			<q-card-actions align="right">
				<q-btn flat :label="$t('Close')" @click="onClose" color="primary" />
			</q-card-actions>
		</q-card>
	</q-dialog>
</template>

<script>
import { defineComponent } from '@vue/composition-api';
import { v4 as uuid } from 'uuid';

import StateList from 'src/modules/StateList/StateList';

export default defineComponent({
	'name': 'DeviceDetailsPopup',
	
	'components': {
		StateList
	},
	
	'computed': {
		deviceId() {
			return this.$store.state.jarvis.selectedDeviceId;
		},
		device() {
			const devices = this.$store.state.jarvis.devices;
			return (this.deviceId && devices[this.deviceId]) || {};
		},
		widget() {
			const items = [];
			for (const stateKey in this.device.states) {
				if (this.device.states[stateKey].state === undefined) {
					continue;
				}
				
				// divider
				if (items.length !== 0) {
					items.push({
						'id': uuid(),
						'type': 'divider'
					});
				}
				
				// state as device
				const state = this.device.states[stateKey];
				items.push({
					'id': uuid(),
					'type': 'device',
					'deviceId': this.device.id,
					'bodyStateKey': stateKey,
					'primaryStateKey': stateKey,
					...state,
					'label': state.label || stateKey,
					'deviceStateDetails': true,
					'_isPopup': true
				});
			}
			
			return { items };
		}
	},
	
	'methods': {
		onClose() {
			this.$store.commit('jarvis/set', {
				'selectedDeviceId': null
			});
		}
	}
});
</script>
