<template>
	<div>
		<q-item clickable v-ripple :class="notification.state || 'unread'" @click="show" :id="'notification-' + notification.id">
			<q-item-section avatar>
				<icon :name="notification.icon || 'mdi-comment'" color="primary" size="24px" />
			</q-item-section>
			
			<q-item-section>
				<q-item-label lines="1">
					<span class="text-weight-medium">{{ notification.title }}</span>
				</q-item-label>
				<q-item-label caption lines="1" v-if="notification.message">
					<div v-html="notification.message" class="overflow-dots" />
				</q-item-label>
			</q-item-section>
			
			<q-item-section side>
				<div class="text-grey-8 q-gutter-xs">
					
					<q-btn v-if="notification.state === 'read'" @click="unread" class="gt-xs" size="12px" flat dense round icon="mdi-circle-outline">
						<q-tooltip>{{ $t('Mark as unread') }}</q-tooltip>
					</q-btn>
					
					<q-btn v-if="notification.state !== 'read' && notification.state !== 'archived'" @click="read" class="gt-xs" size="12px" flat dense round icon="mdi-circle-slice-8">
						<q-tooltip>{{ $t('Mark as read') }}</q-tooltip>
					</q-btn>
					
					<q-btn v-if="notification.state !== 'archived'" @click="archive" class="gt-xs" size="12px" flat dense round icon="mdi-package-down">
						<q-tooltip>{{ $t('Mark as archived') }}</q-tooltip>
					</q-btn>
					
					<q-btn v-if="notification.state === 'archived'" @click="unarchive" class="gt-xs" size="12px" flat dense round icon="mdi-package-up">
						<q-tooltip>{{ $t('Restore from archive') }}</q-tooltip>
					</q-btn>
					
					<q-btn v-if="notification.state === 'archived'" @click="remove" color="negative" class="gt-xs" size="12px" flat dense round icon="delete">
						<q-tooltip>{{ $t('Delete') }}</q-tooltip>
					</q-btn>
				</div>
			</q-item-section>
		</q-item>
		<q-separator/>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	"name": 'NotificationListItem',
	"props": [ 'notification' ],
	
	"methods": {
		preventDefault(e) {
			e.stopPropagation();
		},
		show() {
			this.$store.commit('notifications/setNew', {
				'notifications': [{
					...this.notification,
					'dialog': 'popup',
					'progress': false
				}]
			});
		},
		read(e) {
			this.preventDefault(e);
			this.$store.dispatch('notifications/set', {
				'notificationId': this.notification.id,
				'props': {
					'state': 'read'
				}
			});
		},
		unread(e) {
			this.preventDefault(e);
			this.$store.dispatch('notifications/set', {
				'notificationId': this.notification.id,
				'props': {
					'state': 'unread'
				}
			});
		},
		archive(e) {
			this.preventDefault(e);
			this.$store.dispatch('notifications/set', {
				'notificationId': this.notification.id,
				'props': {
					'state': 'archived'
				}
			});
		},
		unarchive(e) {
			this.preventDefault(e);
			this.$store.dispatch('notifications/set', {
				'notificationId': this.notification.id,
				'props': {
					'state': 'unread'
				}
			});
		},
		remove(e) {
			this.preventDefault(e);
			this.$store.dispatch('notifications/del', {
				'notificationId': this.notification.id
			});
		}
	}
});
</script>

<style lang="scss">
.read {
	opacity: 0.5;
}

.unread {
	background-color: #fff;
}

.read {
	background-color: $grey-4;
}

.archived {
	font-style: italic;
	background-color: $grey-4;
	opacity: 0.5;
}
</style>
