<template>
	<div>
		<div
			v-if="tab.fullscreen !== true"
			class="container"
			:style="{
				'gridTemplateRows': 'repeat(' + containerRows + ', ' + widgetHeight + 'px)',
				'columnGap': widgetGap + 'px',
				'rowGap': widgetGap + 'px',
				'padding': widgetGap + 'px'
			}"
			>
			
			<div
				v-for="container in getWidgetContainers"
				:key="container.i"
				:style="{
					'gridColumn': container.x+1 + ' / span ' + container.w,
					'gridRow': container.y+1 + ' / span ' + container.h
				}"
				class="box"
				>
				
				<widget
					:page="page"
					:tab="tab"
					:widgets="container.items || []"
					:config="container"
					:containerSize="{
						'width': $q.screen.width / 12 * container.w,
						'height': container.h * widgetHeight + ((container.h-1) * widgetGap)
					}"
					/>
			</div>
		</div>
		<div v-else :style="{ 'height': pageHeight + 'px' }">
			<widget
				fullscreen
				:page="page"
				:tab="tab"
				:widgets="fullscreenContainer.items || []"
				:config="fullscreenContainer"
				:containerSize="{
					'width': $q.screen.width,
					'height': pageHeight
				}"
				/>
			
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import Widget from './Widget.vue';

export default defineComponent({
	'name': 'Page',
	'props': [ 'page', 'tab' ],
	
	'components': {
		Widget
	},
	
	'computed': {
		widgetGap() {
			return (this.isSmartphone && this.widgetConfig.smartphoneGapSize) || (this.isTablet && this.widgetConfig.tabletGapSize) || this.widgetConfig.desktopGapSize || 5;
		},
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		containerRows() {
			// container rows are calculated based on the widget with the highest y + its height
			return Math.max.apply(Math, this.getWidgetContainers.map(o => o.y + o.h))
		},
		widgetHeight() {
			const fitPageHeight = (this.isSmartphone && this.widgetConfig.smartphoneFitPageHeight) || (this.isTablet && this.widgetConfig.tabletFitPageHeight) || this.widgetConfig.desktopFitPageHeight || false;
			const widgetHeight = (this.isSmartphone && this.widgetConfig.smartphoneWidgetHeight) || (this.isTablet && this.widgetConfig.tabletWidgetHeight) || this.widgetConfig.desktopWidgetHeight || 100;
			
			return fitPageHeight === true ? ((this.pageHeight - (2 * this.widgetGap)) / this.containerRows - this.widgetGap) : widgetHeight;
		},
		fullscreenContainer() {
			return this.getWidgetContainers?.[0] || {};
		},
		getWidgetContainers() {
			// mobile
			if ((this.isSmartphone && this.widgetConfig.smartphoneSameAs === 'specific-layout') || // smartphone
					(this.isTablet && this.widgetConfig.tabletSameAs === 'smartphone')) { // tablet
				return this.tab.widgetsSmartphone || [];
			}
			
			// tablet
			else if ((this.isTablet && this.widgetConfig.tabletSameAs === 'specific-layout') || // tablet
					(this.isSmartphone && this.widgetConfig.smartphoneSameAs === 'tablet')) { // smartphone
				return this.tab.widgetsTablet || [];
			}
			
			// desktop
			return this.tab.widgetsDesktop || [];
		}
	},
	
	data() {
		this.isSmartphone = this.$q.platform.is.mobile;
		this.isTablet = false;
		this.widgetConfig = this.tab.widgetConfig || {};
		
		return {
		}
	}
});
</script>

<style lang="scss">
.container {
	display: grid;
	grid-template-columns: repeat(12, 1fr);
}

.box {
}
</style>
