import { i18n as VueI18nInstance } from 'src/boot/i18n';
import i18n from './interface';

import { ucFirst } from 'src/helpers/utils';
import { _store } from 'src/store';

import config from 'src/config/config';
import Connection from 'src/controllers/Connection';

i18n.register(VueI18nInstance, i18n.translations);

export default [
	{
		'target': '.q-drawer.q-drawer--left',
		'title': '#drawer.title',
		'message': '#drawer.content',
		'params': {
			'placement': 'right'
		},
		'onEnter': ({ props }) => {
			const socket = Connection.getConnection;
			socket.setState(config.get('NODE_SETTINGS'), JSON.stringify({ ..._store.state.jarvis.settings, ['tour' + ucFirst(props.tour)]: true }));
		}
	},
	{
		'target': '#tabBar',
		'spotlight': '.q-header.jarvis-tabs',
		'title': '#tabs.title',
		'message': '#tabs.content'
	},
	{
		'spotlight': '.jarvis-page',
		'title': '#widgets.title',
		'message': '#widgets.content'
	},
	{
		'target': '.jarvis-widget .jarvis-module-StateList:first-child',
		'title': '#modules.title',
		'message': '#modules.content',
		'params': {
			'placement': 'right-start'
		}
	},
	{
		'target': '.jarvis-widget .jarvis-module-StateList:first-child .jarvis-widget-header',
		'title': '#WidgetHeader.title',
		'message': '#WidgetHeader.content',
		'targetStyle': {
			'background-color': '#fff'
		}
	},
	{
		'buttons': false,
		'onSpotlightClick': ({ tour }) => {
			tour.nextStep();
		},
		
		'target': '.jarvis-widget .jarvis-module-StateList:first-child .jarvis-widget-content',
		'title': '#WidgetContent.title',
		'message': '#WidgetContent.content',
		'targetStyle': {
			'background-color': '#fff'
		},
		'params': {
			'placement': 'right-start'
		}
	},
	{
		'target': '.jarvis-DeviceDetailsPopup',
		'title': '#DeviceStateDetails.title',
		'message': '#DeviceStateDetails.content',
		'params': {
			'placement': 'top'
		}
	}
];
